:root {
  --color-background: white;

  --color-background-result: rgb(135, 78, 249);
  --color-background-result-shadow: rgba(92, 25, 226);
  --color-text-result: white;
}



.App {
  text-align: center;
  font-size: 10px;

  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#result-summary {
  margin: 200px auto;

  width: 600px;
  height: 400px;
  border-radius: 20px;
  background-color: var(--color-background);

  box-shadow: 
  0.5px 0.5px 0px 0px rgb(131 214 255 / 16%), 
  12px 11px 20px 0px rgb(131 170 255 / 12%);

  display: flex;
}

.result-summary-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  width: 50%;
}

/* result-column */

#result-column {
  background-color: var(--color-background-result);
  box-shadow: inset 0px -170px 140px 20px var(--color-background-result-shadow);
  border-radius: 30px;
  color: var(--color-text-result);

}

